export default {
	data() {
		return {
			/* 
				01——身份证
				02——军人证
				03——护照
				04——港澳台通行证
				05——其他
			*/
			certTypes: [{
					id: '01',
					name: '身份证'
				},
				{
					id: '02',
					name: '军人证'
				},
				{
					id: '03',
					name: '护照'
				},
				{
					id: '04',
					name: '港澳台通行证'
				},
				{
					id: '05',
					name: '其他'
				}
			],
			certFront: [],
			certBack: [],
			certType: '',
			info: {
				certType: ''
			},
			// 是否编辑
			isEdit: false
		}
	},
	created() {
		// 获取用户信息
		this.getUserInfo()
	},
	methods: {
		bindPickerChange(e) {
			console.log('picker发送选择改变，携带值为', e.target.value)
			this.certType = this.certTypes[e.target.value].name
		},
		getUserInfo() {
			this.$JRequest('userInfo', data => {
				this.info = data
				let type = this.certTypes.find(v => {
					return v.id === data.certType
				})
				this.certType = type ? type.name : ''
			})
		},
		editBtn() {
			this.isEdit = true
			uni.pageScrollTo({
				scrollTop: 0,
				duration: 300
			})
		},
		enter() {
			let type = this.certTypes.find(v => {
				console.log(v.name, this.certType)
				return v.name === this.certType
			})
			this.info.certType = type ? type.id : ''
			this.$JRequest('userUpdate', this.info, data => {
				this.$JFn.showSuccess('编辑成功')
				this.isEdit = false
			})
		}
	}
}
